import React from 'react'
import pro_monkhood from "../img/projects/project-monkhood.png"
import pro_uvahcare from "../img/projects/project_uvahcare.png"
import pro_dsc from "../img/projects/projects-dsc.png"
import pro_infobility from "../img/projects/project-infobility.png"
import pro_social from "../img/projects/project-social.png"
import pro_hand from "../img/projects/sampleoutput.png"

function Project(props){
    return(
        <div className="col-lg-4 col-md-6 d-flex align-items-stretch" data-aos="zoom-in" data-aos-delay="100">
            <div className="icon-box iconbox-blue">
              <div className="icon">
                <img src={props.img} alt="" />
              </div>
              <h4><a href={props.url} target="_blank">{props.title}</a></h4>
              <p>{props.desc}</p>
            </div>
          </div>
    );
}

export default function Projects() {
  const jarvis="data:image/jpeg;base64,", faceDetector = "data:image/png;base64,", videoCall = "data:image/jpeg;base64," ,chromePic= "data:image/jpeg;base64,"
  return (
    <>
    <section id="services" className="services">
      <div className="container" data-aos="fade-up">

        <div className="section-title">
          <h2>Projects</h2>
        </div>

        <div className="row">
            
            <Project img={pro_monkhood} url="https://monkhood.in" title="Monkhood Website" desc="Made this Website using Mongodb, ExpressJs, ReactJs & NodeJs" />
            <Project img={pro_infobility} url="https://infobility.ca" title="Infobility Website" desc="Made this Website Using NextJs and ReactJs and hosted on AWS Console." />
            <Project img={pro_uvahcare} url="https://smart-canteen-yorr.netlify.app/" title="Smart Canteen" desc="Made this Website using Mongodb, NodeJs, ExpressJs and vanilla Js. " />
            <Project img={pro_dsc} url="https://dsc.gtbit.org" title="GDSC GTBIT Website" desc="Made this Website Using ReactJs and Redux" />
            <Project img={pro_hand} url="https://github.com/RupinVijan/text-to-handwriting" title="Text to Handwriting" desc="Made this Website using Python and its libraries!" />
            <Project img={jarvis} url="https://github.com/RupinVijan/The-Social-Gamer" title="Voice Assistant" desc="Made this Website using purely JavaScript where the assistant respond to any voice Command of the user and replies the user back." />
            <Project img={chromePic} url="https://github.com/RupinVijan/spelling-checker-extension" title="Spell check Extension" desc="Made this Extension Using JavaScript and backend API Using Python-Flask." />
            <Project img={videoCall} url="https://github.com/RupinVijan/VideoChat-FullStack" title="Real Time Video Call App" desc="Made this WebApp Using React JavaScript and backend using Node-Express Js and connections are made using Socket.io." />
            <Project img={faceDetector} url="https://github.com/RupinVijan/face-detection" title="Face Detector" desc="Made This Face Detector Website Using purely JavaScript Which helps the AI method to See Facial emotions of the user." />



        </div>

      </div>
    </section>

    </>
  )
}
